import React, { useState, useEffect } from "react";

import {
  Panel,
  PanelHeader,
  Button,
  Div,
  Group,
  Header,
  Cell,
} from "@vkontakte/vkui";

import { addGroup } from "../bridge-method";

const DefaultPanel = ({ id, go, openAlertPay, setActivePanel, snackbar }) => {
  // useEffect(() => {
  //   addGroup(true, openAlertPay);
  // },[])

  function verificationSubscription() {
    addGroup(openAlertPay, setActivePanel);
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeader></PanelHeader>
      <Div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          marginTop: "150px",
        }}
      >
        <Div
          style={{
            fontWeight: "bold",
            fontSize: "25px",
          }}
        >
          Хотите подписаться на наше сообщество?
        </Div>
        <Button
          onClick={() => addGroup(openAlertPay, setActivePanel)}
          style={{
            width: "200px",
            height: "80px",
            backgroundColor: "#165df5",
            marginTop: "50px",
            borderRadius: "45px",
          }}
        >
          <span
            style={{
              color: "white",
              fontSize: "20px",
            }}
          >
            Подписаться
          </span>
        </Button>
      </Div>
      {snackbar}
    </Panel>
  );
};

export { DefaultPanel };
