import React, { useEffect } from "react";

import {
  Panel,
  PanelHeader,
  Button,
  Div,
  Group,
  FormItem,
  NativeSelect,
} from "@vkontakte/vkui";

import { nativeAds } from "../ads";

const ResultPanel = ({ id, go, IMGresult, openBase }) => {
  useEffect(() => {
    setTimeout(() => {
      nativeAds();
    }, 1000);
  }, []);

  return (
    <Panel id={id} separator={false}>
      <PanelHeader></PanelHeader>

      <Div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          height: "450px",
          // marginTop: "150px",
        }}
      >
        {IMGresult}
        {/* <Div className="icon1"></Div> */}
        <Div>
          <Button
            onClick={openBase}
            style={{
              width: "200px",
              height: "40px",
              backgroundColor: "#165df5",
              marginTop: "5px",
              borderRadius: "10px",
            }}
          >
            <span
              style={{
                color: "white",
                fontSize: "20px",
              }}
            >
              Поделиться
            </span>
          </Button>
        </Div>
        <Div>
          <Button
            onClick={go}
            data-to="home"
            style={{
              width: "200px",
              height: "40px",
              backgroundColor: "#165df5",
              marginTop: "5px",
              borderRadius: "10px",
            }}
          >
            <span
              style={{
                color: "white",
                fontSize: "20px",
              }}
            >
              Пройти еще раз
            </span>
          </Button>
        </Div>
      </Div>
    </Panel>
  );
};

export { ResultPanel };
